import cx from "classnames";
import isEmpty from "lodash/isEmpty";
import React, { useCallback, useState } from "react";
import { withRouter } from "react-router-dom";
import { Button, Card, CardBody, Col, Row } from "reactstrap";

const FlatList = ({
  item,
  onActive,
}) => {
  const {
    avatar,
    email,
    active = true,
    _id,
  } = item;
  const [isActive, setIsActive] = useState(active);
  const onClickActive = useCallback(() => {
    setIsActive(!isActive);
    onActive(!isActive);
  });

  return (
    <Card>
      <CardBody>
        <div className="listing-card">
          <Row>
            <Col
              md="3"
              className="align-items-center col-md-3 d-flex justify-content-center"
            >
              {!isEmpty(avatar) ? (
                <img src={avatar} alt="List" />
              ) : (
                <p>
                  <strong>No Image found</strong>
                </p>
              )}
            </Col>
            <Col md="9" className="text-left">
              <h3>
                {email}
                <i className="fa fa-external-link"></i>
              </h3>
              
              <p className="status">
                Status :
                <span className="mx-1">
                  {isActive ? "Active" : "Not Active"}
                </span>
              </p>
              <Row className=" align-items-center justify-content-between">
            
                <Button
                  active
                  className={cx("btn-small btn-blue btn", {
                    "btn-outline-info": isActive,
                    "btn-outline-success": !isActive,
                  })}
                  style={{ height: "39px", lineHeight: "30px" }}
                  onClick={onClickActive}
                >
                  {isActive ? "UnActive" : "Active"}
                </Button>
                {/* <button className="trash-button">
                  <i
                    className="text-danger far fa-trash-alt"
                    onClick={() => onDelete({ item })}
                  ></i>
                </button> */}
              </Row>
            </Col>
          </Row>
        </div>
      </CardBody>
    </Card>
  );
};

export default withRouter(FlatList);
