import React from "react";
import { connect } from "react-redux";
import { Route, Switch, withRouter } from "react-router-dom";
import { Container, Row } from "reactstrap";
import {
  getAdminApproveListing,
  updateApproveListing,
  removeSelected,
} from "state/AdminDashboard/Epic";
import {
  getUserList,
  updateUser,
} from "state/UserDetails/Epic";
import { selectList } from "state/AdminDashboard/Selectors";
import { selectUserList } from "state/UserDetails/Selectors";
import DashboardMainMenu from "../../components/DashboardMainMenu";
import BaseIndexPage from "../../components/HOC/BaseIndexPage";
import VerticalMenu from "./Components/VerticalMenu";
import Listings from "./Components/listings";
import {
  ApproveMarkSelected,
  ApproveDeleteSelectedSuccess,
} from "state/AdminDashboard/Actions";
import { getSelectedItemIds } from "state/BaseSelector";
import {UserList} from "./Components/user-list";

const Index = ({
  match: { path },
  getAdminApproveListing,
  approvelist,
  userList,
  onSubmitItem,
  onDelete,
  getUserList,
}) => {
  return (
    <div className=" mg-top-90 ">
      <DashboardMainMenu />
      <Container>
        <Row className="py-4">
          <VerticalMenu />
          <Switch>
           
            <Route exact path={`${path}`}>
              <Listings
                onSubmitItem={onSubmitItem}
                list={approvelist}
                getAdminApproveListing={getAdminApproveListing}
                onDelete={onDelete}
              />
            </Route>
            <Route exact path={`${path}/users`}>
            <UserList
                onSubmitItem={onSubmitItem}
                list={userList}
                getUserList={getUserList}
                onDelete={onDelete}
              />
            </Route>
          </Switch>
        </Row>
      </Container>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    noHeader: true,
    approvelist: selectList(state),
    selectedIds: getSelectedItemIds(selectList(state)),
    userList: selectUserList(state),
    noBanner: true,
  };
};

const mapDispatchToProps = (dispatch) => ({
  updateUser: (id, params) => dispatch(updateUser(id, params)),
  update: (id, params) => dispatch(updateApproveListing(id, params)),
  remove: (params) => dispatch(removeSelected(params)),
  markSelected: (item) => dispatch(ApproveMarkSelected(item)),
  refreshList: (list) => dispatch(ApproveDeleteSelectedSuccess(list)),
  getAdminApproveListing: (params) => dispatch(getAdminApproveListing(params)),
  getUserList: (params) => dispatch(getUserList(params)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(BaseIndexPage(Index)));
