import size from "lodash/size";
import map from "lodash/map";
import React, { useEffect, useState } from "react";
import { Col } from "reactstrap";
import FlatList from "./FlatList";
import LoadingContent from "components/global-components/Components/SkeletonLoader/MyListingLoader";
import NoDataFound from "components/global-components/Components/NoData";
import styled from "styled-components";
import { isNil } from "lodash";

const FlatListContainer = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
`;

const Listing = ({
  getAdminApproveListing,
  isLoading,
  title,
  subTitle,
  description,
  onSubmitItem,
  list: updatedList,
  ...props
}) => {
  const [list, setList] = useState(updatedList);

  useEffect(() => {
    getAdminApproveListing().then((res) => {
      if (!isNil(res.data)) {
        setList(res.data);
      }
    });
  }, []);

  useEffect(() => {
    setList(updatedList);
  }, [updatedList]);

  return (
    <Col md="9">
      <div className=" dashboardContent">
        <h1 className="mb-3">{title}</h1>
        <h2 className="sub-title mb-3">{subTitle}</h2>
        <p>{description}</p>
        {isLoading === true ? (
          <div>
            <LoadingContent />
            <LoadingContent />
            <LoadingContent />
          </div>
        ) : size(list) > 0 ? (
          <FlatListContainer>
            {" "}
            {map(list, (item) => (
              <FlatList
                item={item}
                {...props}
                onApprove={(isApproved) => {
                  const {
                    display_id,
                    admin_Approved = false,
                    created_by,
                    property_address,
                  } = item;
                  onSubmitItem(
                    {
                      admin_Approved: isApproved,
                      id: display_id,
                      created_by: created_by,
                      property_address: property_address,
                    },
                    async ({ data }) => {}
                  );
                }}
              />
            ))}
          </FlatListContainer>
        ) : (
          <NoDataFound />
        )}
      </div>
    </Col>
  );
};

export default Listing;
