import { AuthContext } from "Boot/auth/AuthProvider";
import cx from "classnames";
import { PROPERTY } from "components/Routes/Constants";
import isEmpty from "lodash/isEmpty";
import React, { useCallback, useContext, useState } from "react";
import { withRouter } from "react-router-dom";
import { Button, Card, CardBody, Col, Row } from "reactstrap";

const FlatList = ({
  item,
  history,
  onDelete,
  onApprove,
}) => {
  const { user } = useContext(AuthContext);
  const {
    image_attributes,
    property_address,
    created_by,
    display_id,
    admin_Approved,
    _id,
  } = item;
  const [isApproved, setIsApproved] = useState(admin_Approved);
  const onClickApprove = useCallback(() => {
    setIsApproved(!isApproved);
    onApprove(!isApproved);
  });

  return (
    <Card>
      <CardBody>
        <div className="listing-card">
          <Row>
            <Col
              md="3"
              className="align-items-center col-md-3 d-flex justify-content-center"
            >
              {!isEmpty(image_attributes) ? (
                <img src={image_attributes[0].location} alt="List" />
              ) : (
                <p>
                  <strong>No Image found</strong>
                </p>
              )}
            </Col>
            <Col md="9" className="text-left">
              <h3>
                {property_address}
                <i className="fa fa-external-link"></i>
              </h3>
              <p className=" status border-right pr-3">
                ID: <span>{display_id}</span>
              </p>
              <p className="status">
                Status :
                <span className="mx-1">
                  {isApproved ? "Approved" : "Not Approved"}
                </span>
              </p>
              <hr />
              <Row className=" align-items-center justify-content-between">
                <Button
                  className=" btn-small btn-blue btn "
                  style={{ height: "39px", lineHeight: "30px" }}
                  onClick={() => {
                    if (user._id === created_by) {
                      history.push(`${PROPERTY}/${display_id}/#step1`);
                    } else {
                      history.push(`/listing/${display_id}`);
                    }
                  }}
                >
                  View Listing
                </Button>
                <Button
                  active
                  className={cx("btn-small btn-blue btn", {
                    "btn-outline-info": isApproved,
                    "btn-outline-success": !isApproved,
                  })}
                  style={{ height: "39px", lineHeight: "30px" }}
                  onClick={onClickApprove}
                >
                  {isApproved ? "UnApprove" : "Approve"}
                </Button>
                <button className="trash-button">
                  <i
                    className="text-danger far fa-trash-alt"
                    onClick={() => onDelete({ item })}
                  ></i>
                </button>
              </Row>
            </Col>
          </Row>
        </div>
      </CardBody>
    </Card>
  );
};

export default withRouter(FlatList);
