import API from "../../lib/api/Listings";
import {
  ApproveListingsBegin,
  ApproveListingsSuccess,
  ApproveListingsFailure,
  ApproveUpdateBegin,
  ApproveUpdateSuccess,
  ApproveUpdateFailure,
  APPROVE_LISTING
} from "./Actions";
import BaseEpic from "./../BaseEpic";

const baseEpicObject = new BaseEpic();

const { removeSelected } = baseEpicObject.CreateModuleEpics(
  APPROVE_LISTING,
  API
);


const getAdminApproveListing = (params) => async (dispatch) =>
  baseEpicObject.makeCall(
    API.getAdminApproveListing,
    { params },
    ApproveListingsBegin,
    ApproveListingsSuccess,
    ApproveListingsFailure,
    dispatch
  );

  const updateApproveListing = (id, params) => async (dispatch) =>
  baseEpicObject.makeCall(
    API.updateApproveListing,
    { id, params },
    ApproveUpdateBegin,
    ApproveUpdateSuccess,
    ApproveUpdateFailure,
    dispatch
  );

export {
  getAdminApproveListing,
  updateApproveListing,
  removeSelected

};
